@import "@/assets/global.scss";


























































.question-panel {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 24px;
  padding: 0 10px 16px 10px;
  box-sizing: border-box;
  border-radius: 4px;
}

.right-border {
  border: 1px solid #49CE81;
}

.wrong-border {
  border: 1px solid #EB5757;
}

.result-hint {
  @extend .font-14-bold;
  position: absolute;
  top: 0;
  left: 0;
  width: 42px;
  height: 32px;
  padding: 5px 5px;
  border-radius: 4px;
  color: #FFFFFF;
}

.right-hint {
  background: #49CE81;
}

.wrong-hint {
  background: #EB5757;
}

.question-title {
  line-height: 32px;
  text-indent: 40px;
  word-break: break-all;
  text-align: left;
}

.answer-panel {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 16px;
}

.checkbox-container, .radio-container {
  display: block;
  position: relative;
  padding-left: 40px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.checkbox-container input, .radio-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 3px;
  left: 12px;
  height: 16px;
  width: 16px;
}

.checkbox-container .checkmark {
  background-color: #eee;
  box-shadow: 0 1px 1px rgba(97, 121, 133, 0.0528), 0 1px 1px rgba(64, 82, 88, 0.032);
  border-radius: 3px;
}

.radio-container .checkmark {
  box-sizing: border-box;
  border: 1px solid #C4CDD5;
  background-color: #FFF;
  box-shadow: 0 1px 0 rgba(22, 29, 37, 0.05);
  border-radius: 50%;
}

/* When the checkbox is checked, add a blue background */
.checkbox-container input:checked ~ .right-checkmark {
  background-color: #49CE81;
}

.checkbox-container input:checked ~ .wrong-checkmark {
  background-color: #EB5757;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.checkbox-container input:checked ~ .checkmark:after,
.radio-container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.checkbox-container .checkmark:after {
  left: 4px;
  top: 1px;
  width: 4px;
  height: 8px;
  border: solid #FFFFFF;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.radio-container .checkmark:after {
  left: 2px;
  top: 2px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.radio-container .right-checkmark.checkmark:after {
  background-color: #49CE81;
}

.radio-container .wrong-checkmark.checkmark:after {
  background-color: #EB5757;
}

.last-panel {
  display: flex;
  align-items: center;
  margin-top: 4px;

  > div {
    background: #C8553D;
    border-radius: 4px;
    color: #FFFFFF;
    padding: 3px 7px;
    margin-left: 9px;
  }
}
